import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-calc2',
  templateUrl: './calc2.component.html',
  styleUrls: ['./calc2.component.css']
})
export class Calc2Component implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  result: string = "";
  one() {
    this.result+=1;
  }
  two() {
    this.result+=2;
  }
  three() {
    this.result+=3;
  }
  four() {
    this.result+=4;
  }
  five() {
    this.result+=5;
  }
  six() {
    this.result+=6;
  }
  seven() {
    this.result+=7;
  }
  eight() {
    this.result+=8;
  }
  nine() {
    this.result+=9;
  }
  zero() {
    this.result+=0;
  }
  a() {
    this.result+="+";
  }
  s() {
    this.result+="-";
  }
  m() {
    this.result+="*";
  }
  d() {
    this.result+="/";
  }
  equal() {
    this.result=eval(this.result);
  }





}
