import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-calc1',
  templateUrl: './calc1.component.html',
  styleUrls: ['./calc1.component.css']
})
export class Calc1Component implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  x: number ;
  y: number ;
  res: number;
  addd(x,y){
    this.res = x+y;
    
  }

  sub(x,y){
    this.res = x-y;
  }

  multi(x,y){
    this.res = x*y;
  }
divi(x,y){
  this.res = x/y;
}
}
